html, body {
  height: 100%;
  margin: 0;
  padding: 0;
}

#root {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main-content {
  flex-grow: 1;
}

footer {
  background-color: #333;
  color: #fff;
  padding: 1rem 2rem;
  font-family: Karla, sans-serif;
  margin-top: auto;
  flex-shrink: 0;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.footer-left {
  flex: 1;
}

.footer-logo {
  width: 150px;
  height: auto;
}

.footer-right {
  flex: 3;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.footer-column {
  flex: 1;
  min-width: 200px;
}

.footer-column h4 {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}

.footer-column ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer-column ul li {
  margin-bottom: 0.25rem;
}

.footer-column ul li a {
  color: #fff;
  text-decoration: none;
  font-weight: normal;
  padding: 0.1rem 0;
}

.footer-column ul li a:hover {
  text-decoration: underline;
}

.footer-bottom {
  text-align: center;
  margin-top: 1rem;
}

.list_footer ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: block;
}

.list_footer ul li {
  margin-bottom: 0.25rem;
}

.list_footer ul li a {
  color: #fff;
  text-decoration: none;
  font-weight: normal;
  padding: 0.1rem 0;
}

.list_footer ul li a:hover {
  text-decoration: underline;
}

/* Mobile Styles */
@media (max-width: 768px) {
  .footer-container {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .footer-left,
  .footer-right {
    flex: 1 1 100%;
  }

  .footer-column {
    flex: 1 1 100%;
    min-width: auto;
    margin-bottom: 1rem;
  }

  .footer-logo {
    width: 100px;
  }
}

@media (max-width: 768px) {
  .footer-right {
    display: flex;
    flex-wrap: wrap;
  }

  .footer-column {
    flex: 1 1 50%;
    min-width: auto;
    margin-bottom: 1rem;
    box-sizing: border-box;
  }
}
