.menu-container {
  padding: 20px;
  max-width: 800px;
  margin: auto;
  font-family: Arial, sans-serif;
}

.menu-header {
  text-align: center;
  margin-bottom: 40px;
}

.menu-header h1 {
  font-size: 2.5em;
  margin: 0;
  color: #495E57;
}

.menu-header h2 {
  font-size: 1.5em;
  color: #495E57;
  margin: 0;
}

.menu-content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
}

.menu-item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 48%;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.dish-image {
  width: 100%;
  height: 200px;
  object-fit: cover;
}

.dish-details {
  padding: 15px;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.dish-name {
  font-size: 1.2em;
  margin: 0;
}

.dish-description {
  font-size: 1em;
  color: black;
  flex-grow: 1;
}

.dish-price {
  font-size: 1em;
  font-weight: bold;
  margin-top: 10px;
  color: black;
  align-self: flex-end;
}


@media (max-width: 768px) {
  .menu-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 10px;
  }

  .menu-item {
    width: 100%;
  }

  .menu-header h1 {
    font-size: 2em;
  }

  .menu-header h2 {
    font-size: 1.2em;
  }

  .dish-name {
    font-size: 1em;
  }

  .dish-description {
    font-size: 0.9em;
  }

  .dish-price {
    font-size: 0.9em;
  }
}